export const validateIndex = (index, options) => {
  if (index < 0) return 0;
  if (index >= options.length) return options.length - 1;
  return index;
};

export const getOptionIndex = (
  options,
  selectedOption,
  valueProperty = 'value',
) => {
  const index = options.findIndex(
    (option) => option[valueProperty] === selectedOption,
  );
  return validateIndex(index, options);
};

export const isAM = (time) => time && time.hour() < 12;

export const convertTimeString = (string) => {
  if (!string) return null;
  let meridiem = 'AM';
  if (string.toUpperCase().includes('PM')) meridiem = 'PM';

  const arr = string.split(':');
  let hour = parseInt(arr[0], 10);
  if (meridiem === 'PM' && hour !== 12) hour += 12; // formats to 24
  if (meridiem === 'AM' && hour === 12) hour = 0;
  if (meridiem === 'PM' && hour === 12) hour = 12;

  const min = parseInt(arr[1], 10);
  const sec = parseInt(arr[2], 10);

  return {
    hour,
    min,
    sec,
  };
};
