/* eslint-disable complexity */
import React, { useState, useEffect } from 'react';
import moment from 'moment';
import {
  formatMonth,
  formatDay,
  formatYear,
  commonInputProps,
  triggerOnBlur,
} from './utils';
import { onKeyDownHelper } from './helpers';
import { DateInputDivider, DateInputWrapper, DateInputInput } from './styled';
import { DateInputsPropTypes } from './propsTypes';

export const DateInputs = ({ onBlur, onChange, disabled, time, tabIndex }) => {
  const [month, setMonth] = useState('');
  const [day, setDay] = useState('');
  const [year, setYear] = useState('');

  let digits = 2;
  let placeholder = '';

  const onMonthChange = React.useCallback((e) => {
    const format = formatMonth(e.target.value);
    if (format !== false) setMonth(format);
  });
  const onDayChange = React.useCallback((e) => {
    const format = formatDay(e.target.value);
    if (format !== false) setDay(format);
  });
  const onYearChange = React.useCallback((e) => {
    const format = formatYear(e.target.value);
    if (format !== false) setYear(format);
  });
  useEffect(() => {
    onChange(`${month}${day}${year}`);
  }, [day, month, year]);

  useEffect(() => {
    if (
      time &&
      time !== `${month}${day}${year}` &&
      moment(time, 'MMDDYYYY').isValid
    ) {
      const values = String(time || '');
      if (values.slice(0, 2)) setMonth(values.slice(0, 2));
      if (values.slice(2, 4)) setDay(values.slice(2, 4));
      if (values.slice(4, 8)) setYear(values.slice(4, 8));
    } else if (!time && (month || day || year)) {
      setMonth('');
      setDay('');
      setYear('');
    }
  }, [time]);

  const onKeyDown = (event) =>
    onKeyDownHelper(event, {
      onDayChange,
      onMonthChange,
      onYearChange,
    });

  const renderMonthInput = () => {
    placeholder = 'MM';
    const onMonthInputBlur = (e) => {
      triggerOnBlur(e, onBlur);
    };
    return (
      <DateInputInput
        {...commonInputProps(digits, onKeyDown, placeholder)}
        autoComplete="off"
        key="month-input"
        data-testid="month"
        disabled={disabled}
        name="month"
        onBlur={onMonthInputBlur}
        onChange={onMonthChange}
        value={month}
        tabIndex={tabIndex}
      />
    );
  };
  const renderDayInput = () => {
    placeholder = 'DD';
    const onDayInputBlur = (e) => {
      triggerOnBlur(e, onBlur);
    };
    return (
      <DateInputInput
        {...commonInputProps(digits, onKeyDown, placeholder)}
        autoComplete="off"
        key="day-input"
        data-testid="day"
        disabled={disabled}
        name="day"
        onBlur={onDayInputBlur}
        onChange={onDayChange}
        value={day}
        tabIndex={tabIndex}
      />
    );
  };
  const renderYearInput = () => {
    placeholder = 'YY';
    digits = 4;
    const onYearInputBlur = (e) => {
      triggerOnBlur(e, onBlur);
    };
    return (
      <DateInputInput
        {...commonInputProps(digits, onKeyDown, placeholder)}
        autoComplete="off"
        key="year-input"
        data-testid="year"
        disabled={disabled}
        name="year"
        onBlur={onYearInputBlur}
        onChange={onYearChange}
        value={year}
        size="l"
        tabIndex={tabIndex}
      />
    );
  };

  const renderDateInputs = () => {
    const inputs = [renderMonthInput(), renderDayInput(), renderYearInput()];
    const inputsWithDividers = inputs.reduce(
      (acc, input, index) =>
        input
          ? acc.concat([
              index ? <DateInputDivider>/</DateInputDivider> : null,
              input,
            ])
          : acc,
      [],
    );

    return [
      inputsWithDividers,
      React.createElement('span', { key: 'span' }), // hack for next/prev focus
    ];
  };

  return (
    <DateInputWrapper
      disabled={disabled}
      cols={['auto', '6px', 'auto', '6px', 'auto']}
    >
      {renderDateInputs()}
    </DateInputWrapper>
  );
};

DateInputs.propTypes = DateInputsPropTypes;
