/* eslint-disable max-lines */
/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { range } from '@elliemae/ds-utilities/utils';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import moment from 'moment';
import { addLeadingZeros } from '@elliemae/ds-utilities/timeUtils';
import { isAM } from './utils';
import PickerPanel from './PickerPanel';

const blockName = 'time-picker-menu';

const TimePickerContainer = aggregatedClasses('div')(blockName);
const TimePickerHeader = aggregatedClasses('div')(blockName, 'header');
const TimePickerPanelContainer = aggregatedClasses('div')(
  blockName,
  'panel-container',
);

const generateOption = (format) => (option) => ({
  value: option,
  label: typeof format === 'function' ? format(option) : option,
});

export default class TimePickerMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      time: moment(props.time || '', props.format) || moment(),
    };
  }

  componentDidMount() {
    const { time } = this.state;
    this.setState({ time });
  }

  handleMinuteChange(value) {
    const { time } = this.state;
    this.updateTime(time.minute(value));
  }

  handleHoursChange(value) {
    const { time } = this.state;
    const { use12Hours } = this.props;
    let nextHour = value;
    if (use12Hours) {
      if (!isAM(time)) {
        nextHour = (value % 12) + 12;
      }
    }
    this.updateTime(time.hour(nextHour));
  }

  handleSecondsChange(value) {
    const { time } = this.state;
    this.updateTime(time.second(value));
  }

  handleAMPMChange(value) {
    const { time } = this.state;
    const hour = time.hour();
    const nextHour =
      value.toLowerCase() === 'am' ? hour % 12 : (hour % 12) + 12;
    this.updateTime(time.hour(nextHour));
  }

  updateTime(time) {
    const { onTimeChange } = this.props;
    onTimeChange(time);
    this.setState({ time });
  }

  renderHoursPanel() {
    const { time } = this.state;
    const {
      showHours,
      use12Hours,
      format,
      hasHeader,
      disabledTimes,
    } = this.props;
    if (!showHours) return null;

    const hour = time.hour(); // in 24 format
    let selectedHour = use12Hours ? hour % 12 || 12 : hour;
    if (disabledTimes) {
      const { from, to } = disabledTimes;
      if (hour > from.hour && hour < to.hour) {
        if (to.hour < 24) {
          const toFormatted = to.hour % 12 || 12;
          if (selectedHour !== toFormatted) this.handleHoursChange(toFormatted);
          selectedHour = toFormatted;
        } else if (from) {
          selectedHour = (from.hour - 1) % 12 || 12;
        }
      }
    }

    const meridiem = hour >= 12 ? 'PM' : 'AM';
    const withLeadingZeros = format.indexOf('hh') > -1;
    const rangeHours = use12Hours ? range(1, 13) : range(24);
    const hoursOptions = rangeHours.map(
      generateOption(withLeadingZeros && addLeadingZeros(2)),
    );
    return (
      <PickerPanel
        type="hour"
        time={time}
        meridiem={meridiem}
        hasHeader={hasHeader}
        header="H"
        onChange={this.handleHoursChange}
        options={hoursOptions}
        selected={selectedHour}
        disabledTimes={disabledTimes}
      />
    );
  }

  renderMinutesPanel() {
    const { time } = this.state;
    const {
      showMinutes,
      format,
      hasHeader,
      disabledTimes,
      minutesInterval,
    } = this.props;
    if (!showMinutes) return null;
    const withLeadingZeros = format.indexOf('mm') > -1;
    const rangeMinutes = range(0, 60, minutesInterval).map(
      generateOption(withLeadingZeros && addLeadingZeros(2)),
    );

    let selectedMinute = time.minute();
    const selectedHour = time.hour();

    if (disabledTimes) {
      const { from, to } = disabledTimes;
      if (selectedHour <= to.hour && selectedHour > from.hour) {
        if (to) {
          if (selectedMinute !== to.min) this.handleMinuteChange(to.min);
          selectedMinute = to.min;
        } else if (from) {
          if (selectedMinute !== from.min)
            this.handleMinuteChange(from.min - 1);
          selectedMinute = from.min - 1;
        }
      } else {
        selectedMinute = time.minute();
      }
    }

    return (
      <PickerPanel
        type="minutes"
        time={time}
        hasHeader={hasHeader}
        header="M"
        onChange={this.handleMinuteChange}
        options={rangeMinutes}
        selected={selectedMinute}
        disabledTimes={disabledTimes}
      />
    );
  }

  renderSecondsPanel() {
    const { time } = this.state;
    const { showSeconds, format, hasHeader } = this.props;
    if (!showSeconds) return null;
    const withLeadingZeros = format.indexOf('ss') > -1;
    const rangeSeconds = range(60).map(
      generateOption(withLeadingZeros && addLeadingZeros(2)),
    );
    return (
      <PickerPanel
        type="seconds"
        hasHeader={hasHeader}
        header="S"
        onChange={this.handleSecondsChange}
        options={rangeSeconds}
        selected={time.second()}
      />
    );
  }

  renderAMPMPanel() {
    const { time } = this.state;
    const { use12Hours, hasHeader, disabledTimes } = this.props;
    if (!use12Hours) return null;

    let meridiemSelected = !isAM(time) ? 'pm' : 'am';
    if (disabledTimes && disabledTimes.to && meridiemSelected === 'am') {
      meridiemSelected = disabledTimes.to.hour >= 12 ? 'pm' : 'am';
      if (meridiemSelected === 'am' && time.hour() > 12) {
        this.handleAMPMChange('am');
      }

      if (meridiemSelected === 'pm' && time.hour() <= 12) {
        this.handleAMPMChange('pm');
      }
    }

    return (
      <PickerPanel
        type="meridiem"
        hasHeader={hasHeader}
        header=""
        onChange={this.handleAMPMChange}
        options={[
          { value: 'am', label: 'am' },
          { value: 'pm', label: 'pm' },
        ]}
        selected={meridiemSelected}
        disabledTimes={disabledTimes}
      />
    );
  }

  renderHeader = () => {
    const { time } = this.state;
    return <TimePickerHeader>{time.format('hh:mma')}</TimePickerHeader>;
  };

  render() {
    const { hasHeaderDisplay } = this.props;

    return (
      <TimePickerContainer>
        {hasHeaderDisplay && this.renderHeader()}
        <TimePickerPanelContainer>
          {this.renderHoursPanel()}
          {this.renderMinutesPanel()}
          {this.renderSecondsPanel()}
          {this.renderAMPMPanel()}
        </TimePickerPanelContainer>
      </TimePickerContainer>
    );
  }
}

TimePickerMenu.defaultProps = {
  time: moment(),
  hasHeader: false,
  use12Hours: true,
  showSeconds: true,
  showMinutes: true,
  showHours: true,
  disabledTimes: undefined,
  onTimeChange: () => null,
};

TimePickerMenu.propTypes = {
  /** Time value */
  time: PropTypes.instanceOf(moment),
  /** If the menu has header or not */
  hasHeader: PropTypes.bool,
  /** Whether to use the 12 hours format */
  use12Hours: PropTypes.bool,
  /** Whether show 'seconds' or not */
  showSeconds: PropTypes.bool,
  /** Whether show 'minutes' or not */
  showMinutes: PropTypes.bool,
  /** Whether show 'hours' or not */
  showHours: PropTypes.bool,
  /** Handler when time changes */
  onTimeChange: PropTypes.func,
  disabledTimes: PropTypes.object,
};
