/* eslint-disable max-statements */
/* eslint-disable complexity */
/* eslint-disable no-empty-pattern */
/* eslint-disable import/no-unresolved */
/* eslint-disable max-lines */
/* eslint-disable react/prop-types */
import React, { useState, useEffect, useMemo, useRef } from 'react';
import moment from 'moment';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import {
  DSDatePickerController,
  DatePickerDropdown,
} from '@elliemae/ds-date-picker';
import { SingleDatePickerPhrases } from 'react-dates';
import DSInputGroup from '@elliemae/ds-basic/form/InputGroup';
import DSInput from '@elliemae/ds-basic/form/Input';
import { DateInputsV2Impl } from '@elliemae/ds-basic/form/DateInputV2';
import { TimeInputImpl } from '@elliemae/ds-basic/form/TimeInput';
import { getVisibleTimeByFormat } from '@elliemae/ds-utilities/timeUtils';
import { Menu, convertTimeString } from '@elliemae/ds-basic/TimePicker';
import { updateDateAndTime } from '../utils';

const blockName = 'date-time-picker';

const DateTimePickerWrapper = aggregatedClasses('div')(blockName, 'wrapper');
const TimePicker = aggregatedClasses(Menu)(blockName, 'time-picker');
const TimePickerWrapper = aggregatedClasses('div')(
  blockName,
  'time-picker-wrapper',
);
// const DateInput = aggregatedClasses(DateInputsV2Impl)(blockName, 'date-input');
const formatDate = 'MMDDYYYY';

const DSDateTimePickerImpl = ({
  placeholder,
  date,
  datePickerProps: {
    disabled = false,
    numberOfMonths = 1,
    readOnly = false,
    enableOutsideDays = false,
    transitionDuration = 0,
    firstDayOfWeek = 0,
    keepOpenOnDateSelect = true,
    hideKeyboardShortcutsPanel = true,
    onPrevMonthClick = () => null,
    onNextMonthClick = () => null,
    onClose = () => null,
    onDateChange = () => null,
    onError: onErrorDate = () => null,
    phrases = SingleDatePickerPhrases,
    displayFormatDay = 'D',
    isDayBlocked = () => false,
    isOutsideRange = () => false,
    isDayHighlighted = () => false,
    zIndex = 11,
  },
  timePickerProps: {
    format = 'hh:mm A',
    onChange: onChangeTimePicker = () => null,
    disabledTimes = undefined,
    onError: onErrorTime = () => null,
    minutesInterval = 1,
  },
}) => {
  const [selectedDate, setSelectedDate] = useState(undefined);
  const [isOpen, setIsOpen] = useState(false);

  const disabledTimeRange = disabledTimes && {
    from: convertTimeString(disabledTimes.from),
    to: convertTimeString(disabledTimes.to),
  };

  const dateTimePickerWrapperRef = useRef();
  const fullFormat = `${formatDate} ${format}`;

  useEffect(() => {
    if (date) setSelectedDate(moment(date));
  }, [date]);

  const visiblePanels = useMemo(() => getVisibleTimeByFormat(format), []);

  const currentYear = moment().year();

  const handleDatePickerChange = (dateSelected) => {
    const splittedDate = updateDateAndTime(dateSelected, selectedDate);
    if (!readOnly) {
      setSelectedDate(splittedDate);
      onDateChange(splittedDate);
    }
  };

  const onTimeChange = (pickerTime) => {
    const splittedDate = updateDateAndTime(selectedDate, pickerTime);
    onChangeTimePicker(splittedDate);
    setSelectedDate(splittedDate);
  };

  const handleInputChange = (inputValue, type) => {
    let timeBlocked = false;
    const inputMoment = moment(inputValue, fullFormat);
    if (type === 'date') {
      const splittedDate = updateDateAndTime(selectedDate, selectedDate);
      if (inputValue.length < 8) {
        onErrorDate({ message: 'date incomplete', inputValue });
        return;
      }
      if (inputValue.length === 8 && inputMoment.isValid()) {
        setSelectedDate(splittedDate);
        onChangeTimePicker(splittedDate);
      }
      onDateChange(splittedDate);
      return;
    }

    if (disabledTimeRange) {
      const hour = inputValue;
      const min = inputValue;
      const { from, to } = disabledTimeRange;
      if (
        (hour > from.hour && hour < to.hour) ||
        (hour === to.hour && min < to.min)
      ) {
        timeBlocked = true;
      }
    }
    if (timeBlocked) onErrorTime({ message: 'time blocked', inputValue });
    const dateBlocked = isDayBlocked(inputMoment);
    if (dateBlocked) onErrorDate({ message: 'date blocked', inputValue });
    onDateChange(inputValue);
  };

  const handleOutsideClick = (event) => {
    if (!dateTimePickerWrapperRef.current.contains(event.target)) {
      setIsOpen(false);
    }
  };

  const handleClickEscape = () => setIsOpen(false);
  const handleOpen = () => setIsOpen(true);

  return (
    <DSInputGroup
      rightAddon={
        <DatePickerDropdown
          disabled={disabled}
          isOpen={isOpen}
          zIndex={zIndex}
          menu={
            <DateTimePickerWrapper
              ref={dateTimePickerWrapperRef}
              data-testid="date-time-picker"
            >
              <DSDatePickerController
                date={selectedDate}
                displayFormatDay={displayFormatDay}
                enableOutsideDays={enableOutsideDays}
                firstDayOfWeek={firstDayOfWeek}
                hideKeyboardShortcutsPanel={hideKeyboardShortcutsPanel}
                isDayBlocked={isDayBlocked}
                isDayHighlighted={isDayHighlighted}
                isOutsideRange={isOutsideRange}
                keepOpenOnDateSelect={keepOpenOnDateSelect}
                numberOfMonths={numberOfMonths}
                onClickEscape={handleClickEscape}
                onClose={onClose}
                onDateChange={handleDatePickerChange}
                onFocusChange={() => {}}
                onNextMonthClick={onNextMonthClick}
                onOutsideClick={(event) => handleOutsideClick(event)}
                onPrevMonthClick={onPrevMonthClick}
                phrases={phrases}
                transitionDuration={transitionDuration}
              />
              <TimePickerWrapper>
                <TimePicker
                  disabled={disabled}
                  disabledTimes={disabledTimeRange}
                  format={format}
                  hasHeader
                  hasHeaderDisplay
                  onTimeChange={onTimeChange}
                  showSeconds={false}
                  minutesInterval={minutesInterval}
                  time={selectedDate}
                  {...visiblePanels}
                />
              </TimePickerWrapper>
            </DateTimePickerWrapper>
          }
          onClick={handleOpen}
          readOnly={readOnly}
        />
      }
    >
      <DSInput
        customInputType={({ onChange: handleChange, ...restInputProps }) => (
          <>
            <div style={{ display: 'flex' }}>
              <DateInputsV2Impl
                disabled={disabled}
                onChange={(dateSelected) => handleChange(dateSelected, 'date')}
                {...restInputProps}
                yearMaxRange={currentYear + 100}
                yearMinRange={currentYear - 100}
                time={
                  restInputProps.value &&
                  restInputProps.value.hour &&
                  restInputProps.value.isValid()
                    ? restInputProps.value.format(formatDate)
                    : restInputProps.value
                }
              />
            </div>
            <TimeInputImpl
              disabled={disabled}
              format={format}
              onChange={(time) => handleChange(time, 'time')}
              {...restInputProps}
            />
          </>
        )}
        isShowElipsisActive={false}
        disabled={disabled}
        onChange={handleInputChange}
        placeholder={placeholder}
        value={selectedDate}
      />
    </DSInputGroup>
  );
};

export default DSDateTimePickerImpl;
